import React,{useContext} from 'react';
import TodosContext from '../context';

const TodoList = () => {
    const {state, dispatch} = useContext(TodosContext)
    const title = state.todos.length > 0 ? `${state.todos.length} Todos` : "Nothing To Do!";

    return(
        <div className="container mx-auto max-w-md text-center font-mono">
            <h1 className="text-bold">{title}</h1>
            <ul className="list-reset text-white p-0">
                {state.todos.map(todo => (
                    <li 
                        key={todo.id}
                        className="flex items-center bg-orange-dark border-black border-dashed border-2 my-2 py-4"
                    >
                        <span
                            onDoubleClick={() => dispatch({type: "TOGGLE_TODO", payload: todo})} 
                            className= {`flex-1 ml-12 cursor-pointer ${todo.complete && "line-through text-grey-darkest"}` }
                        >
                            {todo.text}
                        </span>
                        <button
                            onClick={() => dispatch({type: "SET_CURRENT_TODO", payload: todo})}
                        >
                            <img 
                                src="https://upload.wikimedia.org/wikipedia/en/thumb/8/8a/OOjs_UI_icon_edit-ltr-progressive.svg/1024px-OOjs_UI_icon_edit-ltr-progressive.svg.png" 
                                alt="Edit Icon"
                                className="h-6"
                            />
                        </button>
                        <button
                            onClick={() => dispatch({type: "REMOVE_TODO", payload: todo})}
                        >
                            <img 
                                src="https://img2.pngio.com/delete-icon-of-glyph-style-available-in-svg-png-eps-ai-icon-delete-icon-png-256_256.png" 
                                alt="Delete Icon"
                                className="h-6"
                            />
                        </button>
                    </li>
                ))}
            </ul>
        </div>
    );
};

export default TodoList;